import React, { useState, useEffect } from "react";
import FoodResult from "./FoodResult.jsx";
import dummyJSON from '../dummy.json';
import edamam from "../assets/edamam.svg";
const config = require("../config.js");
const apiKey = config.edamam;
const apiID = config.id;

//const url = `https://api.spoonacular.com/recipes/findByIngredients`;
const edamame = 'https://api.edamam.com/api/recipes/v2';
export default function Search() {
  const [ingredients, setIngredients] = useState('');
  const [food, setFood] = useState([]);
  const [visible, setVisible] = useState(false);
  let p = `&q=${ingredients}`;
  async function postData() {
    await fetch(edamame+'?type=public'+p+'&app_id='+apiID+'&app_key='+apiKey, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => response.json())
      .then((data) => setFood(data.hits)).catch((error) => {
        console.log(error);
      });
  }
  useEffect(() => {
    //console.log(dummyJSON)
    if (visible) {
      postData();
    }
  }, [ingredients]);
  return (
    <section className="search" id="search">
        <div className="search-container">
        <div className="cols" id="right-col">
          <h1>Find your recipe.</h1>
          <p>Unlock a world of flavors with your pantry essentials. Every ingredient is a potential masterpiece waiting to happen. Let's bring your culinary visions to life. Start by sharing your pantry, and we'll do the rest.</p>
          </div>
          <div className="cc-container">
          <div className="cols" id="left-col">
            <input type="search" id='search-bar' className="search-bar" placeholder="Search ingredients" />
          
        <button className="ingredient-submit" onClick={() => {
          let t = document.getElementById('search-bar');
          setIngredients(t.value);
          if (!visible) setVisible(true);
        }}>
          Search
        </button>
          </div>
          </div>
          
        </div>
        <div className="food-container">
            { visible
              ? food.map((item, index) => {
                return <FoodResult food={item} key={index}/>
              })
              : null
            }
           
          </div>
          <div className="at">
          <img src={edamam} alt="Powered by Edamam" className="attribution"/>
          </div>
    </section>
  );
  
}
