import React, { useRef } from "react";
import 'boxicons'
import emailjs from "@emailjs/browser";
export default function Contact() {
    const form = useRef();

    const sendEmail = (e) => {
      e.preventDefault();
      const contactMessage = document.getElementById('contact-message');
      const button = document.getElementById('submit-button');
      emailjs.sendForm(
        'service_rw122oa',
        'template_a944037',
        form.current,
        'xpY7UemGR-1GYIahJ'
      ).then((result) => {
        contactMessage.textContent = 'Message sent successfully ';
        setTimeout(() => {
          contactMessage.textContent = '';
        }, 5000);
        button.disabled = true;
        button.value = 'Please reload the page';
      }, (error) => {
        contactMessage.textContent = `Error sending message: ${error.text}`;
    })
    }
    return (
        <section className="contact" id="contact">
            <div className="contact-container">
                <div className="contact-left">
                    <div className="head">
                    <h3>Contact us</h3>
                    <h1>Drop us a line</h1>
                    <p>Fill out this form and we'll hop straight to it.</p>
                    
                    </div>
                    <div className="icon-links">
                        <div>Socials</div>
                    <ul>
                        <li className="icon-link"><a href="https://www.linkedin.com/in/bo-hubbard-4988b2246/" description='Linkedin' target="_blank"><box-icon type='logo' name='linkedin-square' size='40px'></box-icon></a></li>
                        <li className="icon-link"><a href="https://bohubbard.xyz" target="_blank"><box-icon type='solid' name='compass' className='icons'size='40px'></box-icon></a></li>
                    </ul>
                    </div>
                    <div className="disclaim">
                    <h2>Disclaimer</h2>
                    <p>We handle inquiries on a case-by-case basis, promising to give yours the spotlight it deserves. So fire away with questions, ideas, or just a friendly 'hello' - we can't wait to dive into the delicious details! </p>
                    </div>
                </div>
                <div className="contact-right">
                    <h2>Send us a message</h2>
                    <form action="" onSubmit={sendEmail} ref={form}>
                        <div className="input-wrap">
                        <label htmlFor="fname">First Name</label>
                        <input className="contact-input" type="text" id="fname" name="firstname" placeholder="First name" size={10} required/>
                        </div>
                        <div className="input-wrap">
                        <label htmlFor="lname">Last Name</label>
                        <input className="contact-input" type="text" id="lname" name="lastname" placeholder="Last name" size={10} required/>
                        </div>
                        
                        <div className="input-wrap w-100">
                        <label htmlFor="email">Email address</label>
                        <input className="contact-input" type="email" name="email" id="email" placeholder="Enter email" size={30} required/>
                        </div>
                        
                        <div className="input-wrap w-100 textarea">
                        <label htmlFor="message">How can we help?</label>
                        <textarea className="contact-input" rows={5} name="message" auto-complete='off' placeholder="Message" required></textarea>
                        </div>
                        <div className="input-wrap w-100">
                            <button type="submit" className="contact-submit" id="contact-submit">Send message</button>
                        </div>
                    </form>
                </div>
            </div>
        </section>
    );
}